import React from 'react'
import devil from '../images/devil.svg'
import spider from '../images/spider.svg'

function Header(props){
    return(
        <div style={{backgroundColor:'#1f0404',textAlign:'center'}}> 
            <div className="header">
                <img src={devil} alt='' width="200px" height="auto"/>
                <h1><u>Welcome to Task 10</u></h1>
                <img src={spider} alt='' width="200px" height="auto"/>
            </div>
            <button onClick={props.rules} style={{textAlign:'center',backgroundColor:'red'}}>Read Rules?</button>
        </div>
    )
}

export default Header