import React from 'react'
//Importing Components
import WeaponCard from './WeaponCard'

//Importing images
import rock from '../images/rock.jpg'
import scissor from '../images/scissor.jpg'
import paper from '../images/paper.jpg'
import win from '../images/win.png'

let AIMove = '' 

class Game extends React.Component{
    constructor(){
        super()
        this.state={
            moves:[{
                name:'rock',
                description:'Blunt Weapon. Effective against scissors',
                imgURL:rock
            },{
                name:'paper',
                description:'More statgic weapon. Effective against rock',
                imgURL:paper
            },{
                name:'scissors',
                description:'Cuts right through paper. Useless against rock',
                imgURL:scissor
            }],
            i:0,
            //The initial values of win lose and draw are set to false by default
            win:false,
            lose:false,
            draw:false
            
        }
        this.changeWeapon= this.changeWeapon.bind(this)
        this.playGame=this.playGame.bind(this)
        this.reset=this.reset.bind(this)
    }

    playGame(){
        
        const AI = Math.floor(Math.random()*3)
        const i = this.state.i
        const moves = ['rock','paper','scissors']
        AIMove = moves[AI]

        //00000000000000000000-Draw-00000000000000000000
        //If the AI move is equal to the users move, its a draw
        if(AI===i){
            console.log("Both ", moves[i], ", Draw")
            this.setState({
                draw:true
            })
        }//END OF DRAW----------------------------------
        
        //00000000000000000000-Lose-00000000000000000000
        //both variables, AI and i, represent a certain move when i use them as an index in the moves array
        //This makes it much shorter for me to write the condistions for winning or losing 
        else if( (i===0 && AI===1) || (i===1 && AI===2) || (i===2 && AI===0) ){
            console.log(moves[i],moves[AI],"lose") 
            this.setState({
                lose:true
            })
        }//END OF LOSE----------------------------------

        //00000000000000000000-Win-00000000000000000000
        //Because I've already specified conditions for a lose and a draw, I have no need to write conditions
        //for winning since there are only 3 outcomes i.e. win, lose or draw
        else{
            console.log(moves[i],moves[AI],"win")
            this.setState({
                win:true
            })
        }//END OF WIN----------------------------------
        console.log(this.state)
        document.getElementById('lightbox').classList.add('active')
    }




    //This function changes the state of a the property which I've called i. This property has two functions.
    /*The first is to choose which card the <WeaponCard> function will display by using it as the index of 
    the moves array which I've added to state.*/ 
    changeWeapon(e){
        if(e.target.id==="right"){
            if(this.state.i===2)return
            else{this.setState({
                i:this.state.i+1
            })}
            //I've given the divs, which act as buttons to scroll through the various Weapon options, id's of right and left
            /*At some point I realised that I only needed to give one of them an ID because im using an if else statement with only two options
            but I kept them there. Perhaps they will make my code more understandable*/
        }else{
            if(this.state.i===0)return
            else{this.setState({
                i:this.state.i-1
            })}
        }
    }

    reset(){
        document.getElementById('lightbox').classList.remove('active')
        //once the game has been played I will once again set these values to false
        this.setState({
            win:false,
            lose:false,
            draw:false
        })
        console.log(this.state)
    }

    render(){
        
        return(
            <div className="game">
                <h1>Choose Your Weapon</h1>
                <div className="game2">
                    <WeaponCard
                    moves={this.state.moves[this.state.i]}
                    changeWeapon={this.changeWeapon}
                    />
                </div>
                <button onClick={this.playGame}>Select</button>
                <button onClick={this.props.play}>Give up?</button>
                <div id="lightbox">
                    <h1>Computer Chose {AIMove}</h1>
                    <h1>{this.state.win? "Congratulations... You win!":null}</h1>
                    <h1>{this.state.lose? "Sorry... You lose!":null}</h1>
                    <h1>{this.state.draw? "It's a draw!":null}</h1>

                    <button onClick={this.reset}>Play Again?</button>
                </div>
                
            </div>
        )
    }
}

export default Game
