import React from 'react';
import {Transition, animated} from 'react-spring/renderprops'

import './App.css';

import Header from './components/Header'
import PlayGame from './components/PlayGame'
import Game from './components/Game'

class App extends React.Component {
  constructor(){
    super()
    this.state={
      PlayGame:false,
      PlayOption:true
    }
    this.play=this.play.bind(this)
  }

  play(){
    this.setState({
      PlayGame:!this.state.PlayGame,
      PlayOption:!this.state.PlayOption
    })
  }
  rules(e){
    document.getElementById('lightbox2').classList.add('active')
  }

  hideRules(){
    document.getElementById('lightbox2').classList.remove('active')
  }

  render(){
    return (
      <div className="app">
        <Header
        rules={this.rules}
        />
        
        <Transition
          native
          items={this.state.PlayOption}
          from={{opacity:0}}
          enter={{opacity:1}}
          leave={{opacity:0}}>
            {show => show && (props =>(
              <animated.div style={props}>
                <PlayGame play={this.play}/>
              </animated.div>
            ))}
        </Transition>        

        <Transition
          native
          items={this.state.PlayGame}
          from={{opacity:0}}
          enter={{opacity:1}}
          leave={{opacity:0}}>
            {show => show && (props =>(
              <animated.div style={props}>
                <Game play={this.play}/>
              </animated.div>
            ))}
        </Transition>
        <div id='lightbox2'>
          <h1>Rules of the game:</h1>
          <p>A player who decides to play rock will beat another player who has chosen scissors 
            ("rock crushes scissors" or sometimes "blunts scissors"), but will lose to one who has played 
            paper ("paper covers rock"); a play of paper will lose to a play of scissors ("scissors cuts paper").</p>
          <button onClick={this.hideRules}>Got it?</button>    
        </div>

      </div>
    )
  }
}

export default App;
