import React from 'react'



function WeaponCard(props){
    

    return(
        <div className="weapon">
            <h1>{props.moves.name}</h1>
            <div id="left" onClick={props.changeWeapon} style={left}></div>
            <img src={props.moves.imgURL} alt='' width="200px" height="auto"/>
            <p>{props.moves.description}</p>
            <div id="right" onClick={props.changeWeapon} style={right}></div>
        </div>
    )
}

export default WeaponCard


//I used specific values for border width and border color to shape the divs into left and right arrows. Then I made their positioin Absolute
const left= {
    borderWidth:'30px 40px 30px 0',
    borderColor: 'transparent black transparent transparent',
    left:'0',
    marginLeft:'30px',
    cursor:'pointer',
    position:'absolute',
    top:'50%',
    margiTop:'-35px',
    width:'0',
    height:'0',
    borderStyle:'solid',
}

const right= {
    borderWidth:'30px 0 30px 40px',
    borderColor: 'transparent transparent transparent black',
    right:'0',
    marginRight:'60px',
    cursor:'pointer',
    position:'absolute',
    top:'50%',
    margiTop:'-35px',
    width:'0',
    height:'0',
    borderStyle:'solid',
}